@import '../common/sass/variables';

.skillsBlock {
  background-color: $bgColor;
  min-height: 120vh;

  .skillsContainer {
    padding: 30px;
    justify-content: flex-start;
    flex-wrap: nowrap;

    .skills {
      width: 100%;
      display: flex;
      justify-content: space-around;
      flex-wrap: wrap;
      @media screen and (max-width: 576px) {
        justify-content: center;
        flex-direction: column;
        align-items: center;

      }
    }
  }

}






